<template>
  <div class="home-container">
    <van-nav-bar title="黑马头条" fixed />
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" :disabled="finished">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <ArticleInfo
          v-for="item in artlist"
          :key="item.id"
          :title="item.title"
          :author="item.aut_name"
          :cmCount="item.comm_count"
          :time="item.pubdate"
          :cover="item.cover"
        >
        </ArticleInfo>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getArticleListAPI } from "@/api/articleAPI.js";
import ArticleInfo from "@/components/Ariticle/ArticleInfo.vue";

export default {
  name: "Home",
  data() {
    return {
      //页码值
      page: 1,
      //每页显示多少条数据
      limit: 10,
      artlist: [],
      //是否正在加载下一页数据，如果loading为true，则不会反复触发load事件
      loading: true,
      //所有数据是否加载完毕了,没数据了就把finished改成true
      finished: false,
      //是否正在下拉刷新
      isLoading: false,
      
    };
  },
  methods: {
    //封装获取文章列表数据的方法
    async initArticleList(isRefresh) {
      //发起get请求
      const { data: res } = await getArticleListAPI(this.page, this.limit);

      if (isRefresh) {
        //下拉刷新
        this.artlist = [ ...res,...this.artlist];

        this.isLoading = false;


        
      } else {
        //上拉加载更多
        this.artlist = [...this.artlist, ...res];

        this.loading = false;
      }

      if (res.length === 0) {
        this.finished = true;
        
        
      }
    },

    onLoad() {
      console.log("ojbk");
      //1.让页码值+1
      this.page++;
      /* if(this.page === 7){this.finished = true} */

      //2.重新请求接口获取数据
      this.initArticleList();
    },
    onRefresh() {
      console.log("触发了下拉刷新");
      //1.页码值+1
      this.page++;

      this.initArticleList(true)
    },
  },
  created() {
    this.initArticleList();
  },
  components: {
    ArticleInfo,
  },
};
</script>

<style lang="less" scoped>
.home-container {
  padding: 46px 0 50px 0; //上右下左
}

</style>